.skills-container{
  height: max-content;
  padding-top: 4rem;
  padding-bottom: 4rem;
}

.skills-container-text {
  display: grid !important;
  justify-content: left;
  align-content: center;
  color: var(--DarkGrey);

}

.skills-title {
  color: var(--DarkGrey);
  font-size: 3rem;
  font-family: "Prototype";
  margin-top: 0rem;
}

.skills-body {
  font-size: 1.6rem;
  padding-top: 1rem;
  padding-bottom: 0rem;
}

.skills-carousel {
  display: flex;
  justify-content: center;
  margin-top: 3%;
  margin-bottom: 2%;
}
@media only screen and (max-width: 680px) {
  .skills-container-text {
    display: grid !important;
    justify-content: left;
    align-content: center;
    color: var(--DarkGrey);

  }
  .skills-title {
    padding-bottom: 1rem;
    font-size: 2.8rem !important;
    text-align: center;
  }

  .skills-body {
    margin: auto -1rem !important;
    line-height: 2.2rem;
    font-size: 1.4rem !important;
  }
}

@media only screen and (max-width: 480px) {
  .skills-carousel {
    display: flex;
    justify-content: center;
  }
  .skills-title {
    text-align: center;
  }
  .skills-body {
    font-size: 1.3rem !important;
  }
}


@media only screen and (max-width: 480px) {
  .skills-carousel {
    display: flex;
    justify-content: center;
  }
  .skills-title {
    text-align: center;
  }
  .skills-body {
    font-size: 1.1rem !important;
  }
}
